<div class="container"><h1 class="text-center my-5">Crypto Trading</h1><div class="card"><div class="card-body"><form class="row row-cols-lg-auto g-3 align-items-center" @submit.prevent="onSubmit" method="post"><div class="col-12"><label class="visually-hidden" for="inputExchange">Exchange</label><select class="form-select" id="inputExchange" v-model="exchange"><option v-for="(text, value) in exchanges" :key="value" :value="value">{{ text }}</option></select></div><div class="col-12"><label class="visually-hidden" for="inputSymbol">Symbol</label><select class="form-select" id="inputSymbol" v-model="symbol"><option v-for="value in symbols" :key="value" :value="value">{{ value }}</option></select></div><div class="col-12"><label class="visually-hidden" for="inputInterval">Interval</label><select class="form-select" id="inputInterval" v-model="interval"><option v-for="value in intervals" :key="value" :value="value">{{ value }}</option></select></div><div class="col-12"><label class="visually-hidden" for="inputIndicator">Indicator</label><select class="form-select" id="inputIndicator" v-model="indicator"><option v-for="(text, value) in indicators" :key="value" :value="value">{{ text }}</option></select></div><div class="col-12"><button class="btn btn-primary" type="submit" :disabled="loading._">Submit</button></div></form><div class="mt-3 table-responsive"><table class="table table-bordered"><thead><tr><th class="text-center align-middle" rowspan="2">Action</th><th class="text-center align-middle" rowspan="2">Time</th><th class="text-center align-middle" rowspan="2">Price</th><th class="text-center align-middle" rowspan="2">Strength</th><th class="text-center align-middle" colspan="3">Divergence 1</th><th class="text-center align-middle" colspan="3">Divergence 2</th></tr><tr><th class="text-center">Time</th><th class="text-center">Price</th><th class="text-center">RSI</th><th class="text-center">Time</th><th class="text-center">Price</th><th class="text-center">RSI</th></tr></thead><tbody><tr v-for="swingTrade in swingTrades"><td class="text-center">{{ swingTrade.buy ? 'Buy' : 'Sell'}}</td><td class="text-center">{{ swingTrade.time }}</td><td class="text-end">{{ swingTrade.price?.toFixed(2) }}</td><td class="text-end">{{ swingTrade.strength.toFixed(2) }}</td><td class="text-center">{{ swingTrade.divergence_1.time }}</td><td class="text-end">{{ swingTrade.divergence_1.price.toFixed(2) }}</td><td class="text-end">{{ swingTrade.divergence_1.rsi.toFixed(2) }}</td><td class="text-center">{{ swingTrade.divergence_2.time }}</td><td class="text-end">{{ swingTrade.divergence_2.price.toFixed(2) }}</td><td class="text-end">{{ swingTrade.divergence_2.rsi.toFixed(2) }}</td></tr></tbody></table></div></div></div></div>